/**
 * Verificando se um objeto  um elemento HTML
 * via: https://stackoverflow.com/questions/384286/how-do-you-check-if-a-javascript-object-is-a-dom-object
 *
 * @param {Object} obj
 * @returns {Boolean}
 */
export default function isHTMLElement (obj) {
  try {
    // Utilizando W3 DOM2 (funciona para FF, Opera e Chrome)
    return obj instanceof HTMLElement
  }
  catch (e) {
    // Navegadores que no possuem suporte a W3 DOM2 no tm HTMLElement
    // e um erro  gerado, terminando aqui, onde algumas propriedades que
    // todos os elementos tm so testadas (funciona no IE7)
    return (typeof obj === 'object')
      && (obj.nodeType === 1)
      && (typeof obj.style ===  'object')
      && (typeof obj.ownerDocument === 'object')
  }
}
